<template>
	<view class="register">
		<view class="formBox" style="margin-top: 20rpx;" v-show="show==1">
			<u-form :model="form" ref="uForm">
				<u-form-item label="身高" prop="high" label-width='140'>
					<u-input v-model="form.high" input-align="right" />
				</u-form-item>
				<u-form-item label="胸围" prop="chestMeasurement" label-width='180'>
					<u-input v-model="form.chestMeasurement" input-align="right" />
				</u-form-item>
				<u-form-item label="腰围" prop="waistline" label-width='140'>
					<u-input v-model="form.waistline" input-align="right" />
				</u-form-item>
				<u-form-item label="头围" prop="headCircumference" label-width='180'>
					<u-input v-model="form.headCircumference" input-align="right" />
				</u-form-item>
				<u-form-item label="体重" prop="weight" label-width='140'>
					<u-input v-model="form.weight" input-align="right" />
				</u-form-item>
				<u-form-item label="脚长" prop="footLength" label-width='180'>
					<u-input v-model="form.footLength" input-align="right" />
				</u-form-item>
				<u-form-item label="血型" prop="bloodGroup" label-width='180'>
					<u-input v-model="form.bloodGroup" input-align="right" />
				</u-form-item>
			</u-form>
			<u-button @click="submit" type="primary" style="margin-top: 50rpx;">保存</u-button>
		</view>
		<u-toast ref="uToast"></u-toast>
	</view>
</template>

<script>
	import {
		bodyList,
		saveAntiStature
	} from '../../api/index.js'
	import request from '@/api/request.js'
	export default {
		data() {
			return {
				show: 1,
				susheArr: [],
				sushe_show: false,
				action: request.ROOTHTTP+"/file/upload",
				fileList: [],
				form: {
				},
			}
		},
		onLoad() {
			bodyList({
				id: uni.getStorageSync('userInfo').id
			}).then(res => {
				if (res.code == 200) {
					this.form = res.rows[0];
				}
			});
		},
		methods: {
			submit() {
				this.$refs.uForm.validate(valid => {
					if (valid) {
						saveAntiStature(this.form).then(res => {
							if (res.code == 200) {
								this.$refs.uToast.show({
									title: '保存成功'
								});
							}
						});
					} else {
						console.log('验证失败');
					}
				});
			}
		}
	}
</script>

<style scoped>
	.register {
		background-color: #F5F6FA;
		width: 100vw;
		height: 100vh;
	}

	.register /deep/ .u-list-item {
		width: 125rpx !important;
		height: 125rpx !important;
	}

	.register .title {
		width: 100%;
		text-align: center;
		font-size: 30rpx;
		font-weight: bold;
		color: #333333;
		padding: 32rpx 0 25rpx 0;
	}

	.mybox {
		width: 100%;
		text-align: center;
	}

	.mybox /deep/ .u-upload {
		justify-content: center;
	}

	.formBox {
		padding: 0 30rpx 30rpx 30rpx;
		background-color: #fff;
	}

	.mititle {
		font-size: 30rpx;
		font-weight: bold;
		color: #333333;
		height: 90rpx;
		line-height: 90rpx;
		margin: 0 -30rpx;
		padding: 0 30rpx;
		border-bottom: 1px solid #f1f1f1;
	}

	.smtitle {
		width: 100%;
		text-align: center;
		font-size: 24rpx;
		color: #B1B1B1;
	}

	.smtitle b {
		color: #FF9D1D;
	}
</style>